<template>
  <div class="image">
    <img v-bind:src="`https://img.maximemoreillon.com/image?id=${image._id}`">

    <div class="">
      <div class="">
        ID: {{image._id}}
      </div>
      <div class="">
        Size: {{image.size/1000}}kB
      </div>
      <div class="">
        Upload date: {{image.upload_date}}
      </div>
      <div class="">
        URL:
        <input
          type="text"
          readonly
          ref="image_url"
          v-bind:value="`https://img.maximemoreillon.com/image?id=${image._id}`">
        <content-copy-icon
          v-on:click="copy_url()"/>
      </div>
    </div>
  </div>
</template>

<script>
import ContentCopyIcon from 'vue-material-design-icons/ContentCopy.vue';


export default {
  name: 'ImageInfo',
  props: {
    image: Object
  },
  components: {
    ContentCopyIcon,
  },
  methods: {
    copy_url() {
      let url_input = this.$refs.image_url
      /* Select the text field */
      url_input.select();
      url_input.setSelectionRange(0, 99999); /*For mobile devices*/

      /* Copy the text inside the text field */
      document.execCommand("copy");

      console.log("copied")
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.image {
  border: 1px solid #dddddd;
  margin: 5px 0;
  padding: 5px;
  display: flex;
}

.image img{
  width: 100px;
  height: 100px;
  object-fit: contain;
  margin-right: 5px;
}
</style>
