<template>
  <div class="list">

    <ImageInfo
      v-for="image in images"
      v-bind:key="image._id"
      v-bind:image="image"/>


  </div>
</template>

<script>
// @ is an alias to /src
import ImageInfo from '@/components/ImageInfo.vue';

export default {
  name: 'Home',
  data(){
    return {
      images: []
    }
  },
  components: {
    ImageInfo,
  },
  mounted(){
    this.get_images()
  },
  methods: {
    get_images(){
      this.axios.get('https://img.maximemoreillon.com/list')
      .then(response => {
        this.images.splice(0,this.images.length)
        response.data.forEach((image) => {
          this.images.push(image)
        });
      })
    }
  },
}
</script>

