<template>
  <div class="home">
    <form
      class=""
      v-on:submit.prevent="submit()"
      v-if="!url">
      <input type="file" ref="image_input" name="image">
      <input type="submit" name="">
    </form>
    <div class="" v-else>
      <input type="text" readonly v-bind:value="url">
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
//import HelloWorld from '@/components/HelloWorld.vue'

export default {
  name: 'Home',
  components: {

  },
  data(){
    return {
      url: null
    }
  },
  methods: {
    submit(){

      let formData = new FormData();
      formData.append('image', this.$refs.image_input.files[0]);
      this.axios.post(`https://img.maximemoreillon.com/upload`, formData, {
        headers: {'Content-Type': 'multipart/form-data' }
      })
      .then(response => {
        console.log(response.data)
        this.url = `https://img.maximemoreillon.com/image?id=${response.data._id}`
      })
      .catch(error => console.log(error));

    }
  }
}
</script>
